import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import{Work} from '../../models/work.model';
import workJSON from '../../../assets/JSON/Work.json';

@Injectable({
  providedIn: 'root'
})
export class WorksService {

  // constructor(private fireStore: AngularFirestore) {
  // }
  // /**
  //  * Lista de Trabajos
  //  * @returns
  //  */
  // cargarTrabajos(): Observable<any> {
  //   return this.fireStore.collection("trabajos", ref => ref.orderBy('orden', 'desc')).snapshotChanges();
  // }

  listaTrabajos: Work[] = [];

  constructor() {
    this.cargarwork();
  }

  /**
   * Lista de Certificados
   */
  cargarwork() {

    for (const item in workJSON.work) {
      let titulo = new Work();
      titulo.titulo = workJSON.work[item].titulo;
      titulo.imagen = workJSON.work[item].imagen;
      titulo.descripcion = workJSON.work[item].descripcion;
      titulo.descripcionEN = workJSON.work[item].descripcionEN;
      titulo.url = workJSON.work[item].url;
      titulo.fechaInicio = workJSON.work[item].fechaInicio;
      titulo.fechaFin = workJSON.work[item].fechaFin;
      this.listaTrabajos.push(titulo);
    }

  }

}
