import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { Project } from '../../models/project.model';
import projectJSON from '../../../assets/JSON/Proyect.json';

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  listaProyecto: Project[] = []

  constructor() {
    this.cargarProyectos();
  }

  /**
   * Lista de Proyectos Personales
   * @returns
   */
  cargarProyectos(){
    //return this.fireStore.collection("proyectos").snapshotChanges();
    for (const item in projectJSON.proyecto) {
      let titulo = new Project();
      titulo.titulo = projectJSON.proyecto[item].titulo;
      titulo.imagen = projectJSON.proyecto[item].imagen;
      titulo.imagen2 = projectJSON.proyecto[item].imagen2;
      titulo.descripcion = projectJSON.proyecto[item].descripcion;
      titulo.descripcionEN = projectJSON.proyecto[item].descripcionEN;
      titulo.url = projectJSON.proyecto[item].url;
      titulo.lenguaje = projectJSON.proyecto[item].lenguaje;
      this.listaProyecto.push(titulo);
    }

  }
}
